import Shape from "./Shape";
import "./shape.css";

const Card = () => {
  return (
    <div className="card shimmer">
      <Shape class_name="rectangle" theme="dark" height="150px" />
      <Shape class_name="line" theme="light" />
      <Shape class_name="line" theme="dark" />
      <Shape class_name="line" theme="light" />
    </div>
  );
};

export default Card;
